import { useState, useEffect, useCallback } from 'react'

import axios from 'axios'

import Row from 'react-bootstrap/Row'
import Navbar from 'react-bootstrap/Navbar'
import ProgressBar from 'react-bootstrap/ProgressBar'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'

const BottomNavbar = ({ count, stripePromise, children }) => {
  const [isLoadingGroup, setLoadingGroup] = useState(false)
  const [isLoadingCrypto, setLoadingCrypto] = useState(false)
  const [isLoadingShares, setLoadingShares] = useState(false)
  const [isLoadingOptions, setLoadingOptions] = useState(false)
  const [isLoadingForex, setLoadingForex] = useState(false)
  const [isLoadingMember, setLoadingMember] = useState(false)

  const checkoutClickGroup = useCallback(async (event) => {
    setLoadingGroup(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-group');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingGroup, stripePromise])

  useEffect(() => {
    if (isLoadingGroup) {
      checkoutClickGroup().then(() => {
        setLoadingGroup(false);
      });
    }
  }, [checkoutClickGroup, isLoadingGroup]);

  const checkoutClickCrypto = useCallback(async (event) => {
    setLoadingCrypto(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-crypto');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingCrypto, stripePromise])

  useEffect(() => {
    if (isLoadingCrypto) {
      checkoutClickCrypto().then(() => {
        setLoadingCrypto(false);
      });
    }
  }, [checkoutClickCrypto, isLoadingCrypto]);

  const checkoutClickShares = useCallback(async (event) => {
    setLoadingShares(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-shares');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingShares, stripePromise])

  useEffect(() => {
    if (isLoadingShares) {
      checkoutClickShares().then(() => {
        setLoadingShares(false);
      });
    }
  }, [checkoutClickShares, isLoadingShares]);

  const checkoutClickOptions = useCallback(async (event) => {
    setLoadingOptions(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-options');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingOptions, stripePromise])

  useEffect(() => {
    if (isLoadingOptions) {
      checkoutClickOptions().then(() => {
        setLoadingOptions(false);
      });
    }
  }, [checkoutClickOptions, isLoadingOptions]);

  const checkoutClickForex = useCallback(async (event) => {
    setLoadingForex(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-forex');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingForex, stripePromise])

  useEffect(() => {
    if (isLoadingForex) {
      checkoutClickForex().then(() => {
        setLoadingForex(false);
      });
    }
  }, [checkoutClickForex, isLoadingForex]);

  const checkoutClickMember = useCallback(async (event) => {
    setLoadingMember(true);
    const stripe = await stripePromise;
    const response = await axios.post('https://alertigo-api.herokuapp.com/create-checkout-member');
    const session = await response.data;

    const result = await stripe.redirectToCheckout({
      sessionId: session.id,
    });
    if (result.error) {
      console.log(result.error.message);
    }
  }, [setLoadingMember, stripePromise])

  useEffect(() => {
    if (isLoadingMember) {
      checkoutClickMember().then(() => {
        setLoadingMember(false);
      });
    }
  }, [checkoutClickMember, isLoadingMember]);

  const signUpText = (members) => {
    let dollars = 0;
    let tier = 0;
    if (members < 10) { dollars = 50; tier = 10 }
    else if (members < 25) { dollars = 105; tier = 25 }
    else if (members < 50) { dollars = 125; tier = 50 }
    else if (members < 100) { dollars = 200; tier = 100 }
    else return 'All slots filled! Grab some popcorn while you watch us win :)'

    return tier - members + ' slots open for $' + dollars + '/mo pricing';
  }

  return (
    <>
      <Navbar bg='dark' className='pt-5 pb-4 pl-5 pr-5'>
        <Row className='align-items-center m-auto'>
          <Col xs={12} md={6} className='text-center mb-4 pl-4 pr-4'>
            <Button size='lg' variant='primary' className='shadow text-dark p-4'
              disabled={isLoadingGroup || count >= 100}
              onClick={!isLoadingGroup ? checkoutClickGroup : null}
            >
              <h1 className='font-weight-light'>{count >= 100 ? "CAN'T" : ''} {isLoadingGroup ? 'LOADING...' : 'SUBSCRIBE TO ALERTIGO+'}</h1>
              <h4>{isLoadingGroup ? "Welcome to financial freedom" : signUpText(count)}</h4>
            </Button>
          </Col>
          <Col xs={12} md={6} className='text-primary pl-4 pr-4'>
            <h3 className={(count > 0 ? 'w-100' : 'd-none')}>$50/MONTH:</h3>
            <ProgressBar className={(count > 0 ? 'w-100 mb-4 shadow-lg' : 'd-none')} min={0} max={10} now={count} label={((count / 10) * 100) > 25 ? ((count > 10 ? 10 : count) + '/10') : ''} />
            <h3 className={(count - 10 > 0 ? 'w-100' : 'd-none')}>$105/MONTH:</h3>
            <ProgressBar className={(count - 10 > 0 ? 'w-100 mb-4 shadow-lg' : 'd-none')} min={10} max={25} now={count} label={(((count - 10) / 25) * 100) > 25 ? ((count > 25 ? 15 : count - 10) + '/15') : ''} />
            <h3 className={(count - 25 > 0 ? 'w-100' : 'd-none')}>$125/MONTH:</h3>
            <ProgressBar className={(count - 25 > 0 ? 'w-100 mb-4 shadow-lg' : 'd-none')} min={25} max={50} now={count} label={(((count - 15) / 50) * 100) > 25 ? ((count > 50 ? 25 : count - 25) + '/25') : ''} />
            <h3 className={(count - 1500 > 0 ? 'w-100' : 'd-none')}>$200/MONTH:</h3>
            <ProgressBar className={(count - 1500 > 0 ? 'w-100 mb-4 shadow-lg' : 'd-none')} min={1500} max={3000} now={count} label={(((count - 1500) / 1500) * 100) > 25 ? ((count - 1000 > 1500 ? 1500 : count - 1000) + '/1500') : ''} />
          </Col>
        </Row>
      </Navbar>
      <Container fluid as={Row} className='p-0 m-0 justify-content-center bg-dark'>
        <Col xs={11} sm={5} xl={2}>
          <Button size='lg' variant='primary' className='shadow text-dark p-2 mb-4 w-100'
            disabled={isLoadingCrypto}
            onClick={!isLoadingCrypto ? checkoutClickCrypto : null}
          >
            <h2 className='font-weight-light'>{isLoadingCrypto ? 'LOADING...' : 'SUBSCRIBE TO CRYPTO ALERTS'}</h2>
            <h4>{isLoadingCrypto ? "Welcome to financial freedom" : "$60/mo"}</h4>
          </Button>
        </Col>
        <Col xs={11} sm={5} xl={2}>
          <Button size='lg' variant='primary' className='shadow text-dark p-2 mb-4 w-100'
            disabled={isLoadingShares}
            onClick={!isLoadingShares ? checkoutClickShares : null}
          >
            <h2 className='font-weight-light'>{isLoadingShares ? 'LOADING...' : 'SUBSCRIBE TO SHARES ALERTS'}</h2>
            <h4>{isLoadingShares ? "Welcome to financial freedom" : "$30/mo"}</h4>
          </Button>
        </Col>
        <Col xs={11} sm={5} xl={2}>
          <Button size='lg' variant='primary' className='shadow text-dark p-2 mb-4 w-100'
            disabled={isLoadingOptions}
            onClick={!isLoadingOptions ? checkoutClickOptions : null}
          >
            <h2 className='font-weight-light'>{isLoadingOptions ? 'LOADING...' : 'SUBSCRIBE TO OPTIONS ALERTS'}</h2>
            <h4>{isLoadingOptions ? "Welcome to financial freedom" : "$50/mo"}</h4>
          </Button>
        </Col>
        <Col xs={11} sm={5} xl={2}>
          <Button size='lg' variant='primary' className='shadow text-dark p-2 mb-4 w-100'
            disabled={isLoadingForex}
            onClick={!isLoadingForex ? checkoutClickForex : null}
          >
            <h2 className='font-weight-light'>{isLoadingForex ? 'LOADING...' : 'SUBSCRIBE TO FOREX ALERTS'}</h2>
            <h4>{isLoadingForex ? "Welcome to financial freedom" : "$40/mo"}</h4>
          </Button>
        </Col>
        <Col xs={11} sm={10} xl={9}>
          <Button size='lg' variant='primary' className='shadow text-dark p-2 mt-4 mb-5 w-100'
            disabled={isLoadingMember}
            onClick={!isLoadingMember ? checkoutClickMember : null}
          >
            <h2 className='font-weight-light mb-0'>{isLoadingMember ? 'LOADING...' : 'SUBSCRIBE TO ALL ALERTS'}</h2>
            <h4 className='mb-0'>{isLoadingMember ? "Welcome to financial freedom" : "$125/mo"}</h4>
          </Button>
        </Col>
      </Container>
    </>
  )
}
export default BottomNavbar