import {useState} from 'react'

import axios from 'axios';

import Modal from 'react-bootstrap/Modal'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

const CreateAlertModal = ({showCreateModal, closeCreateModal, userName, alertType}) => {
    const [ticker, setTicker] = useState('')
    const [entry, setEntry] = useState('')
    const [exit, setExit] = useState('')
    const [stance, setStance] = useState(true)
    const [postDiscord, setPostDiscord] = useState(false)
    const [synopsis, setSynopsis] = useState('')
    const [valid, setValid] = useState(false)

    const onSubmit = (e) => {
        const form = e.currentTarget;
        setValid(true)
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            const newAlert = {
                'ticker': ticker,
                'stance': stance,
                'entry': entry,
                'exit': exit,
                'synopsis': synopsis,
                'time': new Date(),
                'author': userName,
                'postDiscord': postDiscord
            }
            axios.post('https://alertigo-api.herokuapp.com/'+ alertType + 'Alerts/add', newAlert)
                .then(res => {
                    setTicker('')
                    setStance(true)
                    setEntry('')
                    setExit('')
                    setSynopsis('')
                    setPostDiscord(false)
                    setValid(false)
                    closeCreateModal()
                })
            e.preventDefault()
        }
    }

    return (
        <Modal size='lg' centered 
        show={showCreateModal} onHide={closeCreateModal}
        >
            <Modal.Header className='bg-primary border-0 text-dark' closeButton>
                <Modal.Title >POST A NEW ALERT</Modal.Title>
            </Modal.Header>
            <Modal.Body className='bg-secondary rounded-bottom p-3'>
                <Form noValidate validated={valid} onSubmit={onSubmit}>
                    <Row className='text-center'>
                        <Form.Group as={Col} xs={12} lg={4}>
                            <Form.Control
                                onChange={e => setTicker(e.target.value)}
                                placeholder="TICKER"
                                className='bg-primary border-0 shadow'
                                required
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} lg={4}>
                            <Form.Control
                                onChange={e => setEntry(e.target.value)}
                                placeholder="ENTRY"
                                className='bg-primary border-0 shadow'
                                required
                                    />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} lg={4}>
                            <Form.Control 
                                onChange={e => setExit(e.target.value)}
                                placeholder="EXIT"
                                className='bg-primary border-0 shadow'
                                required
                            />
                        </Form.Group>
                    </Row>
                    <Row className='justify-content-center text-center align-items-center'>
                        <Form.Group as={Col} xs={12}>
                            <Form.Control as='textarea'
                                rows='2'
                                onChange={e => setSynopsis(e.target.value)}
                                placeholder="SYNOPSIS (OPTIONAL)"
                                className='bg-primary border-0 shadow'
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} sm={4} required className='m-0'>
                            <Form.Check inline 
                                type="radio" 
                                checked={stance} 
                                onChange={() => setStance(true)}
                                label="BULLISH"
                            />
                            <Form.Check inline 
                                type="radio"
                                checked={!stance} 
                                onChange={() => setStance(false)}
                                label="BEARISH"
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} sm={4} className='m-0'>
                            <Form.Check 
                                checked={postDiscord}
                                onChange={() => setPostDiscord(!postDiscord)}
                                label="Discord?"
                            />
                        </Form.Group>

                        <Form.Group as={Col} xs={12} sm={4} className='mt-3 mt-sm-0 mb-0 text-center'>
                            <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button>
                        </Form.Group>
                    </Row>
                </Form>
            </Modal.Body>
        </Modal>
    )
}

export default CreateAlertModal