import {useEffect, useState} from 'react'

import { Link } from 'react-router-dom';

import axios from 'axios';

import Modal from 'react-bootstrap/Modal'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

const EditAlertModal = ({showEditModal, closeEditModal, userName, alertType}) => {
    const [ticker, setTicker] = useState('')
    const [entry, setEntry] = useState('')
    const [exit, setExit] = useState('')
    const [stance, setStance] = useState(true)
    const [postDiscord, setPostDiscord] = useState(false)
    const [synopsis, setSynopsis] = useState('')
    const [valid, setValid] = useState(false)

    useEffect(() => {
        const url = window.location.href
        const id = url.substring(url.lastIndexOf('/') + 1);
        if(id.length === 24) {
            axios.get('https://alertigo-api.herokuapp.com/' + alertType + 'Alerts/'+ id)
                .then(res => {
                  if (res.data !== null) {
                    setTicker(res.data.ticker)
                    setEntry(res.data.entry)
                    setExit(res.data.exit)
                    setStance(res.data.stance)
                    setSynopsis(res.data.synopsis)
                    setPostDiscord(res.data.postDiscord)
                  }
                })
        }
    }, [showEditModal, alertType])
    
    const deleteAlert = () => {
        let url = window.location.href
        let id = url.substring(url.lastIndexOf('/') + 1);

        axios.post('https://alertigo-api.herokuapp.com/' + alertType + 'Alerts/remove/'+id)
            .then(res => {
                closeEditModal()
            })
    }

    const onSubmit = (e) => {
        let url = window.location.href
        let id = url.substring(url.lastIndexOf('/') + 1);

        const form = e.currentTarget;
        setValid(true)
        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            const newAlert = {
                'ticker': ticker,
                'stance': stance,
                'entry': entry,
                'exit': exit,
                'synopsis': synopsis,
                'time': new Date(),
                'author': userName,
                'postDiscord': postDiscord
            };
            axios.post('https://alertigo-api.herokuapp.com/'+ alertType + 'Alerts/update/' + id, newAlert)
                .then(res => {
                    setTicker('')
                    setStance(true)
                    setEntry('')
                    setExit('')
                    setSynopsis('')
                    setPostDiscord(false)
                    setValid(false)
                    closeEditModal()
                });
            e.preventDefault()
        }
    }

    return (
        <Modal size='lg' centered show={showEditModal} onHide={closeEditModal}>
            <Modal.Header className='bg-primary border-0 text-dark' closeButton>
                <Modal.Title>EDIT AN ALERT</Modal.Title>
            </Modal.Header>
            <Modal.Body className='bg-secondary rounded-bottom'>
                <Form noValidate validated={valid} onSubmit={onSubmit}>
                    <Row className='text-center'>
                        <Form.Group as={Col} xs={12} lg={4}>
                            <Form.Control 
                                value={ticker}
                                onChange={e => setTicker(e.target.value)}
                                placeholder="TICKER"
                                className='bg-primary border-0 shadow'
                                required
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} lg={4}>
                            <Form.Control 
                                value={entry}
                                onChange={e => setEntry(e.target.value)}
                                placeholder="ENTRY"
                                className='bg-primary border-0 shadow'
                                required
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} lg={4}>
                            <Form.Control 
                                value={exit}
                                onChange={e => setExit(e.target.value)}
                                placeholder="EXIT"
                                className='bg-primary border-0 shadow'
                                required
                            />
                        </Form.Group>
                    </Row>
                    <Row className='text-center justify-content-center align-items-center'>
                        <Form.Group as={Col} xs={12}>
                            <Form.Control as='textarea' rows='2'
                                value={synopsis}
                                onChange={e => setSynopsis(e.target.value)}
                                placeholder="SYNOPSIS (OPTIONAL)"
                                className='bg-primary border-0 shadow'
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} md={3} required className='mb-md-0'>
                            <Form.Check inline 
                                type="radio" 
                                checked={stance} 
                                onChange={() => setStance(true)}
                                label="BULLISH"
                            />
                            <Form.Check inline 
                                type="radio"
                                checked={!stance} 
                                onChange={() => setStance(false)}
                                label="BEARISH"
                            />
                        </Form.Group>
                        <Form.Group as={Col} xs={6} md={3} className='mb-md-0'>
                            <Form.Check 
                                checked={postDiscord}
                                onChange={() => setPostDiscord(!postDiscord)}
                                label="Post to Discord?"
                            />
                        </Form.Group>
                        <Form.Group xs={6} md={3} as={Col} className='mb-0'>
                            <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button> 
                        </Form.Group>
                        <Form.Group xs={6} md={3} as={Col} className='mb-0'>
                            <Link to={'/' + alertType + 'Alerts'} onClick={deleteAlert}>
                                <Button variant='primary' className='text-dark'>Delete</Button>
                            </Link>
                        </Form.Group>
                    </Row>
                </Form>
            </Modal.Body>
        </Modal>
    )
}

export default EditAlertModal