import { Link } from 'react-router-dom';

import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import Accordion from "react-bootstrap/Accordion";

import AlertCardToggle from './alert-card-toggle.component'

const AlertCard = ({showEditModal, showChartModal, setTicker, admin, alert, alertKey}) => {

    const formatDate = (time) => {
        let [year, month, date] = time.split("-")
        let newDate = date.substring(0, 2)
        let newYear = year.substring(2, 4)
        return month+'/'+newDate+'/'+newYear
    }

    return (
        <Card key={alertKey} bg='primary' text='dark' 
                className='shadow border-0 mt-4 mb-4'>
            <AlertCardToggle alertKey={alertKey} alert={alert}/>
            <Accordion.Collapse eventKey={alertKey}>
                <Card.Body className='p-0 text-center text-dark border-secondary border-top'>
                    <p style={{display: alert.synopsis === '' ? 'none' : 'block'}} 
                    className='mt-3 pl-3 pr-3'>
                        {alert.synopsis}
                    </p>
                    <Card.Footer className='border-0'>
                        <small className="text-dark text-muted mt-3">
                            Posted by {alert.author} on {formatDate(alert.time)}
                        </small><br/>
                        <Button className='shadow text-dark mt-2 mb-2 ml-3'
                            variant='secondary' onClick={() => {setTicker(alert.ticker); showChartModal()}}>
                          Chart
                        </Button>
                        {admin &&
                            <Link to={'/alerts/' + alert._id}>
                                <Button className='shadow text-dark mt-2 mb-2 ml-3' 
                                variant='secondary' onClick={showEditModal}>
                                    Edit Alert
                                </Button>
                            </Link>
                        }
                    </Card.Footer>
                </Card.Body>
            </Accordion.Collapse>
        </Card>
    )
}

export default AlertCard