import {useContext} from 'react'

import Accordion from "react-bootstrap/Accordion";
import AccordionContext from "react-bootstrap/AccordionContext";
import Button from 'react-bootstrap/Button'

const AlertCardToggle = ({ children, alertKey, alert }) => {
    const currentEventKey = useContext(AccordionContext);
    const isCurrentEventKey = currentEventKey === alertKey;
    
    const formatTime = (time) => {
        let offset = (new Date().getTimezoneOffset())/60
        let timeStr = time.split("T")
        let hours = timeStr[1].substring(0,2) - offset
        let mins = timeStr[1].substring(3,5)
        let ampm = ''
        if(hours>12) {
            hours-=12
            ampm="PM"
        } else if (hours===0) {
            hours=12
            ampm="AM"
        } else if(hours<0) {
            hours = 12+hours
            ampm="PM"
        } else {
            ampm="AM"
        }
        return hours + ':' + mins + ampm
    }

    return (
        <Accordion.Toggle 
            style={{
                'borderBottomRightRadius': isCurrentEventKey ? '0' : '', 
                'borderBottomLeftRadius': isCurrentEventKey ? '0' : '' 
            }}
            block className='text-dark' 
            as={Button}  variant='primary' 
            eventKey={alertKey}
        >   
            <h2 className='mb-0 text-uppercase'>{alert.ticker}</h2>
                <h5 className='mb-1 text-uppercase'>{(alert.stance ? 'BULLISH' : 'BEARISH')}</h5>
                <h6 className='mb-0 text-uppercase'>ENTRY TARGET: {alert.entry}</h6>
                <h6 xs={12} className='mb-0 text-uppercase'>EXIT TARGET: {alert.exit}</h6>
                <small>LAST UPDATE: {formatTime(alert.time)} </small>
        </Accordion.Toggle>
    );
}

export default AlertCardToggle