import {useState} from 'react';

import axios from 'axios';

//react bootstrap imports
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";


const CreateAccount = () => {
    const [valid, setValid] = useState(false)
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [crypto, setCrypto] = useState(false)
    const [shares, setShares] = useState(false)
    const [options, setOptions] = useState(false)
    const [forex, setForex] = useState(false)
    const [admin, setAdmin] = useState(false)
    const [member, setMember] = useState(false)

    const setAllAlerts = (enabled) => {
        setCrypto(enabled)
        setShares(enabled)
        setOptions(enabled)
        setForex(enabled)
    }

    const onSubmit = (e) => {
        const form = e.currentTarget

        setValid(true)

        if (form.checkValidity() === false) {
            e.preventDefault();
            e.stopPropagation();
        } else {
            const newAccount = {
                name: name,
                email: email,
                crypto: crypto,
                shares: shares,
                options: options,
                forex: forex,
                admin: admin
            }
            axios.post('https://alertigo-api.herokuapp.com/accounts/add', newAccount)
                .then(res => {
                    setName('')
                    setEmail('')
                    setCrypto(false)
                    setShares(false)
                    setOptions(false)
                    setForex(false)
                    setAdmin(false)
                    setMember(false)
                })
            e.preventDefault()
        }
    }

    return (
        <Container className='text-center text-dark pt-5 pb-5'>
            <h1 className='mb-5'>CREATE AN ACCOUNT</h1>
            <Form noValidate validated={valid} onSubmit={onSubmit}>
                <Row className='justify-content-center'>
                    <Form.Group as={Col} xs={6} md={4} lg={3}>
                        <Form.Control
                            onChange={(e) => setName(e.target.value)}
                            value={name}
                            placeholder="NAME"
                            className='bg-primary border-0 shadow'
                            required
                                />
                    </Form.Group>
                    <Form.Group as={Col} xs={6} md={4} lg={3}>
                        <Form.Control 
                            onChange={(e) => setEmail(e.target.value)}
                            value={email}
                            placeholder="EMAIL"
                            className='bg-primary border-0 shadow'
                            required
                                />
                    </Form.Group>
                </Row>
                <Row className='bg-primary shadow rounded w-50 ml-auto mr-auto mt-3 pt-3 pl-1 pr-1'>
                    <Form.Group as={Col} xs={6} lg={3}>
                        <Form.Check 
                            disabled={admin || member}
                            checked={crypto || admin || member}
                            label='Crypto'
                            onChange={() => setCrypto(!crypto)}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs={6} lg={3}>
                        <Form.Check 
                            disabled={admin || member}
                            checked={shares || admin || member}
                            label='Shares'
                            onChange={() => setShares(!shares)}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs={6} lg={3}>
                        <Form.Check 
                            disabled={admin || member}
                            checked={options || admin || member}
                            label='Options'
                            onChange={() => setOptions(!options)}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs={6} lg={3}>
                        <Form.Check 
                            disabled={admin || member}
                            checked={forex || admin || member}
                            label='Forex'
                            onChange={() => setForex(!forex)}
                        />
                    </Form.Group>
                </Row>
                <Row className='bg-primary shadow rounded w-50 ml-auto mr-auto mt-3 pt-3 pl-1 pr-1'>
                    <Form.Group as={Col} xs={6}>
                        <Form.Check 
                            disabled={member}
                            checked={admin}
                            label='Admin'
                            onChange={() => {setAllAlerts(!admin); setAdmin(!admin)}}
                        />
                    </Form.Group>
                    <Form.Group as={Col} xs={6}>
                        <Form.Check 
                            disabled={admin}
                            checked={member || admin}
                            label='Member'
                            onChange={() => {setAllAlerts(!member); setMember(!member)}}
                        />
                    </Form.Group>
                </Row>
                <Row className='justify-content-center'>
                    <Form.Group className='mb-0 mt-3'>
                        <Button type='submit' className='text-dark shadow' variant="primary" >Submit</Button>
                    </Form.Group>
                </Row>
            </Form>
        </Container>
    )
}

export default CreateAccount