import '../../scss/custom.scss'

import Modal from 'react-bootstrap/Modal'

import Chart from '../site/chart.component'

const AlertChartModal = ({showModal, closeModal, symbol}) => {
  return (
    <Modal size='lg' centered 
      show={showModal} onHide={closeModal}
      className='p-5'
    >
      <Modal.Header className='bg-primary border-0 text-dark text-uppercase' closeButton>
        <Modal.Title>{symbol}</Modal.Title>
      </Modal.Header>
      <Modal.Body className='bg-secondary'>
        <Chart symbol={symbol} autosize className='m-5'/>
      </Modal.Body>
    </Modal>
  )
}

export default AlertChartModal