import TradingViewWidget from 'react-tradingview-widget'

const Chart = ({symbol, interval, autosize, showName}) => {
  return (
    <div className='text-center'>
      {showName &&
        <h3 className='mb-3'>{symbol}</h3>
      }
      <TradingViewWidget 
        symbol={symbol}
        interval={interval ? interval : '15'}
        theme='Dark'
        hide_top_toolbar={true}
        autosize={autosize ? true : false}
        hide_legend={true}
      />
    </div>
  )
}

export default Chart